@import url(https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Baloo 2",cursive}.app{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.topbar{height:3rem;width:100%;background-color:#ffb617}.wrapper{display:-webkit-flex;display:flex}
.sidebar{background-color:#fdc44a}.menu-list>li:first-child{margin-top:1rem}.menu-list a{height:2rem}.menu-list a:hover{background-color:unset;color:#363636}.items-title:hover{background-color:unset}.list-items{display:-webkit-flex;display:flex;height:4rem;font-size:1rem;padding:.3rem .5rem;width:13rem;padding-left:1.5rem}.list-items:hover{background-color:#ffdd92}.items-title{margin-top:.6rem}.list-icons{height:2.4rem;width:2.4rem;margin-top:.25rem}
.profile-cards-wrapper{display:-webkit-flex;display:flex}.cards-wrapper{display:-webkit-flex;display:flex}.card{width:12rem;height:22.3rem;margin:0 .3rem 1rem .3rem;line-height:1.3rem}.card-content{padding:.15rem .8rem 0rem}.profile{width:16rem;height:20rem;margin:.3rem;display:-webkit-flex;display:flex}.profile-name{margin-left:.5rem;margin-bottom:.5rem;width:45.8rem;height:4.8rem;background-color:#ffdd92;padding:1rem}.profile-experience{margin-left:.5rem;width:45.8rem;height:10rem;background-color:#ffdd92;padding:1rem;margin-bottom:.5rem}.profile-experience>ul>li{list-style-type:disc;margin-left:2rem}.profile-card-content{margin-left:.5rem;width:45.8rem;height:4rem;background-color:#ffdd92;padding:1rem}.card-img{height:15rem;width:12rem}
.exp-cards-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.exp-card{width:100%;height:13rem;margin:1rem .3rem 1rem .3rem;line-height:1.3rem}.card-header{box-shadow:2px -1px 20px 1px #ececec;height:5.5rem;margin-bottom:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.position{font-weight:700;padding-top:.65rem;padding-left:1rem;font-size:1.1rem}.location{padding-top:.2rem;padding-left:1rem}.date{padding-top:.2rem;padding-left:1rem}.exp-card-content{padding:.15rem .8rem 0rem}.exp-card-content>li{list-style-type:disc;margin-left:1rem}
.proj-cards-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.proj-card{width:59.5rem;height:24rem;margin-top:1rem;margin-left:.3rem}.proj-wrapper{display:-webkit-flex;display:flex}.proj-card-header{font-weight:700;height:3rem;padding:1rem;box-shadow:2px -1px 20px 1px #ececec}.proj-card-image{height:20rem;width:30rem}.proj-card-content{margin-left:.5rem;width:45.8rem;height:20rem;background-color:#ffdd92;padding:1rem}




